/* to top */
.#{$spec-mod} {
	.back-to-top {
		box-sizing: border-box;
		color: inherit;
		background: none !important;
		box-shadow: 0 0 0 2px currentColor;

		& > i {
			position: relative;
			width: 4px;
			margin-right: 14px;
			background: none;
			background-color: currentColor;

			&::before {
				content: "";
				display: block;
				position: absolute;
				top: 0;
				left: 50%;
				width: 14px;
				height: 14px;
				border: 0 solid currentColor;
				border-width: 4px 0 0 4px;
				transform: rotate(45deg);
				margin: 0 0 0 -9px; } }

		& > span {
			transition: none; }

		// hover
		&:hover {
			& > i {
				margin-right: 20px; } } }

	.back-to-top:hover {
		background-color: $spec-bg-color !important; }

	&.schemecolor-white {
		.back-to-top:hover {
			background-color: $spec-bg-color--scheme-wb !important; } }

	&.schemecolor-blue {
		.back-to-top:hover {
			background-color: $spec-bg-color--scheme-blue !important; } } }

